import React, { useEffect, useState } from 'react';
import {
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  IconButton,
  Paper,
} from '@material-ui/core';
import { Pagination } from '@material-ui/lab';
import { useStyles } from './style';
import { AdminLayout } from 'layouts';
import { useGlobalContext } from 'store';
import { Spinner } from 'components';

const NotifyCation = () => {
  const classes = useStyles();
  const { getUserInfomation } = useGlobalContext();
  const [noteArray, setNoteArray] = useState([]);
  const [loading, setLoading] = useState(false);
  const [totalPage, setTotalPage] = useState();
  const [pageCurrent, setPageCurrent] = useState(1);

  useEffect(() => {
    const run = async () => {
      setLoading(true);
      const res = await getUserInfomation(pageCurrent, 8);
      if (res.success) {
        if (res.total % 8 !== 0) {
          setTotalPage(Math.floor(res.total / 8) + 1);
        } else {
          setTotalPage(res.total / 8);
        }
        setNoteArray(res.data);
        setLoading(false);
      } else {
        setNoteArray([]);
        setLoading(false);
      }
    };
    run();
  }, [pageCurrent]);
  const handeChangePage = (i, page) => {
    setPageCurrent(page);
  };
  return (
    <AdminLayout>
      {loading && <Spinner />}
      <TableContainer className={classes.tableNote} component={Paper}>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell width="30%" align="left">
                H tn
              </TableCell>
              <TableCell width="70%" align="left">
                Email
              </TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {noteArray?.map((item, index) => (
              <TableRow
                key={index}
                sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
              >
                <TableCell width="30%" component="th" scope="row">
                  {item.name}
                </TableCell>
                <TableCell width="70%" align="left">
                  {item.email}
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>

        <Pagination
          count={totalPage}
          color="primary"
          className={classes.pagination}
          onChange={handeChangePage}
        />
      </TableContainer>
    </AdminLayout>
  );
};

export default NotifyCation;
